.rightbar {
  flex: 3.5;
}

.rightbarWrapper {
  padding: 20px 20px 0 0;
}

.birthdayContainer {
  display: flex;
  align-items: center;
}

.birthdayImg {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.birthdayText {
  font-weight: 300;
  font-size: 15px;
}

.rightbarAd {
  width: 100%;
  border-radius: 10px;
  margin: 30px 0;
}

.rightbarFriendList {
  padding: 0;
  margin: 0;
  list-style: none;
}

.rightbarTitle {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}

.rightbarInfo {
  margin-bottom: 30px;
}

.rightbarInfoItem {
  margin-bottom: 10px;
}

.rightbarInfoKey {
  font-weight: 500;
  margin-right: 15px;
  color: #555;
}

.rightbarInfoValue {
  font-weight: 300;
}

.rightbarFollowings {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  /* justify-content: space-between; */
}

.rightbarLinks {
  text-decoration: none;
}

.rightbarFollowing {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  cursor: pointer;
}

.rightbarFollowingImg {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 5px;
  margin: 5px;
}

.rightbarFollowingName {
  color: black;
}

.rightbarFollowButton {
  margin-top: 30px;
  margin-bottom: 10px;
  border: none;
  background-color: #1872f2;
  color: white;
  border-radius: 5px;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}

.rightbarFollowButton:focus {
  outline: none;
}

.flexContainer {
  display: flex;
  flex-direction: row;
}

.profileEdit {
  /* width: 12px;
  height: 12px; */
  border-radius: 50%;
  /* background-color: pink; */
  /* position: absolute; */
  /* top: 2px;
  right: 15px; */
  /* border: 2px solid black; */
}

.editIcon {
  margin: 20px 5px;
  border-radius: 50%;
  border: 2px solid white;
  background-color: white;
}

.editIcon:hover {
  background-color: #d9d9d9;
  cursor: pointer;
}

.menu {
  /**/
}

.cityfromEdit {
  /* border: none; */
  width: 80%;
}

.cityfromEdit:focus {
  /* outline: none; */
  font-size: 14px;
}

.uploadButton {
  border: none;
  padding: 7px;
  border-radius: 5px;
  background-color: green;
  font-weight: 500;
  margin-right: 20px;
  cursor: pointer;
  color: white;
}

.cancelButton {
  border: none;
  padding: 7px;
  border-radius: 5px;
  background-color: red;
  font-weight: 500;
  margin-right: 20px;
  cursor: pointer;
  color: white;
}
