.post {
  width: 100%;
  max-width: 100vw;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  margin: 30px 0;
}

.postWrapper {
  padding: 10px;
}

.postTop {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.postTopLeft {
  display: flex;
  align-items: center;
}

.postProfileImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

.postUsername {
  font-size: 15px;
  font-weight: 500;
  margin: 0 10px;
}

.postDate {
  font-size: 12px;
}

.postCenter {
  margin: 20px 0;
}

.postText {
  word-wrap: break-word;
}

.postImg {
  margin-top: 20px;
  width: 100%;
  max-height: 500px;
  object-fit: contain;
}

.postBottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.postBottomLeft {
  display: flex;
  align-items: center;
}

.likeIcon {
  width: 24px;
  height: 24px;
  margin-right: 5px;
  cursor: pointer;
}

.postLikeCounter {
  font-size: 15px;
}

.postCommentText {
  cursor: pointer;
  border-bottom: 1px dashed gray;
  font-size: 15px;
}

.cancelButton {
  cursor: pointer;
  color: black;
  font-size: 14px;
}

.postEdit {
  border: none;
  width: 80%;
}

.postEdit:focus {
  outline: none;
  font-size: 14px;
}

.updateForm {
  border-bottom: 1px solid gray;
  margin: 10px;
}

.updateButoon {
  border: none;
  padding: 7px;
  border-radius: 5px;
  background-color: green;
  font-weight: 500;
  margin-right: 20px;
  cursor: pointer;
  color: white;
  margin-bottom: 10px;
}

.cancelButton {
  border: none;
  padding: 7px;
  border-radius: 5px;
  background-color: red;
  font-weight: 500;
  margin-right: 20px;
  margin-left: 10px;
  cursor: pointer;
  color: white;
}

/* wider than 600px */
@media screen and (min-width: 600px) {
  .post {
    max-width: 95vw;
  }
}

/* wider than 800px */
@media screen and (min-width: 800px) {
  .post {
    max-width: 60vw;
  }
}

/* wider than 1100px */
@media screen and (min-width: 1100px) {
  .post {
    max-width: 55vw;
  }
}
